	module.exports = {"data":{"post":{"timeToRead":1,"canonicalURL":"https://blog.iansinnott.com/the-wonders-of-css-frameworks/","html":"<p>Today was my first dive into CSS frameworks. Creating layout from scratch is perfectly doable but also perfectly tedious. I have yet to meet a developer who enjoys that aspect of it. We like the stages where we get to build shiny things on a site. So here is what I got into today:</p>\n<ul>\n<li><a href=\"http://cssgrid.net/\">The 1140 Grid System</a></li>\n<li><a href=\"http://imperavi.com/kube/\">Kube</a></li>\n</ul>\n<p>The first one has been around for quite some time. The original developer actually stopped updating it because after creating it he learned so much that it lost it's use. That's awesome, but for a ton of developers it's still a great tool. I would say it's best feature is simplicity. It's not the most robust framework but if you want something really light and simple to set up try it out.</p>\n<p>The second, Kube, is another fully responsive grid system. I didn't really look into Kube's layout features because I was already using 1140, but I'm sure it's good. I really like Kube's design elements that make it super simple to add very sophisticated styling to elements in a few seconds. The one drawback to me was that it is built with LESS instead of Sass. I actually run into this fairly often. It seems as though the majority of the well known frameworks are built with LESS.</p>\n<p>So, instead of sit around and pout I converted a number of Kube's features into Sass. Now I'm developing with a mix of these two grid systems. Kube mainly for UI elements and 1140 for layout. I'm considering actually rolling these together into something usable and Sass based (finally) so that anyone else can enjoy it without switching to LESS. I'll post an update with that is finished.</p>","frontmatter":{"created":"2013-02-07T08:00:00.000Z","title":"The Wonders of CSS Frameworks","disqusId":[1070486878,1070486878]}}},"pathContext":{"id":"/Users/jandrix/dev/sites/blog.iansinnott.com/content/_posts/2013-02-07-the-wonders-of-css-frameworks.md absPath of file >>> MarkdownRemark","next":{"frontmatter":{"title":"I'm famous on GoBodyLanguage.com!","created":"2013-02-20T08:00:00.000Z"},"slug":"im-famous-on-gobodylanguage-com"},"prev":{"frontmatter":{"title":"WordPress Theming From Scratch","created":"2013-01-11T08:00:00.000Z"},"slug":"wordpress-theming-from-scratch"}}}

